<template>
  <div class="forget">
    <van-nav-bar title="忘记密码" left-text="返回" left-arrow @click-left="onClickLeft" />

    <van-form @submit="onSubmit">
      <van-field
        v-model="phone"
        name="手机号"
        label="手机号"
        placeholder="手机号"
        autocomplete="false"
        :rules="[{pattern: phonePattern,required: true, message: '请填写正确手机号' }]"
      />
      <van-field
        v-model="password"
        type="text"
        name="新密码"
        label="新密码"
        placeholder="请填写六位新密码"
        autocomplete="false"
        :rules="[{pattern: pwdPattern,required: true, message: '请填写六位数字新密码' }]"
      />
      <div style="margin: 16px;">
        <van-button color="#7f5839" round block type="info" native-type="submit">确认</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import Api from "../api/api";
import Util from "../util/utils";
import { mapMutations } from "vuex";
export default {
  data() {
    return {
      phone: "",
      password: "",
      phonePattern: /^1[3456789]\d{9}$/,
      pwdPattern: /^\d{6}$/
    };
  },
  methods: {
    ...mapMutations(["changeLogin"]),
    onClickLeft() {
      this.$router.go(-1);
    },
    onSubmit() {
      // 成功提交，后台处理
      Util.showLoading(this, "正在找回");
      Api.retrievePassword({ password: this.password, phone: this.phone }).then(
        res => {
          if (res.data.code == "L001") {
            this.$toast(res.data.msg);
            // 清理token
            Utils.removeCookies(this);
            // 改变login状态
            this.changeLogin({ bool: false });
            this.$router.push({ name: "Login" });
          }
          this.password = "";
          this.$toast(res.data.msg);
        }
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.forget {
  height: 100%;
  background-color: #eee;
}
/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}

/deep/ .van-form {
  margin-top: 50px;
}
</style>